import * as lodash from 'lodash';
import * as Gulp from 'gulp';
import * as path from 'path';
import * as webpack from 'webpack';
import * as fsx from 'fs-extra';
import { EOL } from 'os';

import { WebpackTask } from '@microsoft/gulp-core-build-webpack';
import { KarmaTask } from '@microsoft/gulp-core-build-karma/lib/KarmaTask';
import {
  ISetWebpackPublicPathPluginOptions,
  SetPublicPathPlugin
} from '@microsoft/set-webpack-public-path-plugin';
import { IClientSideComponentManifest } from '@microsoft/sp-module-interfaces';
import {
  IConfigBundle,
  IConfigComponent
} from './../configJson/index';

import { getExternalsKeys } from './ExternalsProcessor';
import { ConfigureExternalBundlingWebpackTask } from './ConfigureExternalBundlingWebpackTask';
import OdspGulpTask, { ILocaleMap, IResolveResourcesResult } from './../OdspGulpTask';
import { getFullHash } from './../utilities/FileUtils';

export interface IConfigureWebpackTaskConfig {
  webpack?: WebpackTask;
  webpackPublicPathOptions?: ISetWebpackPublicPathPluginOptions;
  libraryName?: string;
  libraryTarget?: 'amd' | 'var' | 'this' | 'commonjs' | 'commonjs2' | 'umd' | 'window' | 'assign' | 'jsonp';
  configureExternalBundlingWebpackTask?: ConfigureExternalBundlingWebpackTask;

  /**
   * If set, drop the webpack stats object and visualization in this directory in addition to the temp directory.
   *
   * @internal
   */
  statsObjectsDropLocation?: string;

  /**
   * An optional callback can be provided that allows the generated webpack configuration to be modified.
   */
  additionalConfiguration?: (generatedConfiguration: webpack.Configuration) => webpack.Configuration;

  /**
   * An optional list of linked externals to include. For example, if you want to bundle react, list "react" in this
   *  array. This is only designed to be used in very special cases and is not intended to be used regularly.
   */
  linkedExternalsToBundle?: string[];
}

export interface IBundleComponent extends IConfigComponent {
  manifestData: IClientSideComponentManifest;
  manifestPath: string;
}

export interface IBundleEntry {
  bundleName: string;
  nonLocalizedEntrypointPath?: string;
  localizedEntrypointPaths?: ILocaleMap;
  components: { [id: string]: IBundleComponent };
}

export function getComponentsBundleId(manifests: IClientSideComponentManifest[]): string {
  return manifests.map((manifest) => `${manifest.id}_${manifest.version}`).join('+');
}

export function getBundleId(bundle: IBundleEntry): string {
  const manifests: IClientSideComponentManifest[] = [];
  Object.keys(bundle.components).forEach((id) => manifests.push(bundle.components[id].manifestData));
  return getComponentsBundleId(manifests);
}

export const fileLoaderExts: string[] = ['jpg', 'png', 'woff', 'eot', 'ttf', 'svg', 'gif'];

/**
 * Configures the @microsoft/gulp-core-build-webpack task with some smart defaults based on the package configuration.
 *
 * Example:
 *  IN:
 *    setConfig({
 *      webpack: <reference to webpack task>,
 *      webpackPublicPathOptions: <options for @microsoft/loader-set-webpack-public-path>,
 *      libraryName: <optional library name - set this if the library should be a global on the window>,
 *      configureExternalBundlingWebpackTask: <optional reference to ConfigureExternalBundlingWebpackTask task>
 *    })
 *    buildConfig.properties.entries = [{
 *      "entry": "./lib/App.js",
 *      "manifest": "./src/app.manifest.json",
 *      "outputPath": "dist/app.bundle.js"
 *    }]
 *    buildConfig.properties.externals = {
 *      "react-dom": "node_modules/react-dom/dist/react-dom.js",
 *      "react": "node_modules/react/dist/react.js",
 *      "flux": "node_modules/flux/dist/flux.js",
 *    }
 *
 *  OUT:
 *    set webpack config to (approximately):
 *    {
 *        context: ... root path ...,
 *        entry: { ... mapping of names to entries ... },
 *        resolve: {
 *          modules: [ ... root path + /node_modules ],
 *          alias: { }
 *        },
 *        output: {
 *          library: <library name, if provided>,
 *          libraryTarget: <library target, if specified> or 'amd',
 *          path: ... dist folder ...,
 *          filename: '[name].js',
 *          devtoolModuleFilenameTemplate: 'webpack:///../[resource-path]',
 *          devtoolFallbackModuleFilenameTemplate: 'webpack:///../[resource-path]?[hash]'
 *        },
 *        externals: [ ... externals names ... ],
 *        module: {
 *          noParse: [ /\.map$/ ],
 *          rules: [
 *            {
 *              test: /\.css$/,
 *              use: [
 *                {
 *                  loader: `@microsoft/load-themed-styles'
 *                },
 *                {
 *                  loader: 'css-loader',
 *                  options: {
 *                    minimize: true
 *                  }
 *                }
 *              ]
 *            },
 *            {
 *              test: /\.(jpg|png|woff|eot|ttf|svg|gif)$/,
 *              use: [
 *                {
 *                  loader: '@microsoft/loader-cased-file',
 *                  options: {
 *                    name: '[name:lower]_[hash].[ext]'
 *                  }
 *                }
 *              ]
 *            },
 *            {
 *              test: /\.html$/,
 *              use: [
 *                {
 *                  loader: 'html-loader'
 *                }
 *              ]
 *            }
 *          ]
 *        },
 *        plugins: []
 *      }
 */
export class ConfigureWebpackTask extends OdspGulpTask<IConfigureWebpackTaskConfig> {
  private _baseConfig: webpack.Configuration = {
    module: {
      rules: [
        {
          use: [
            {
              loader: require.resolve('@microsoft/loader-load-themed-styles'),
              options: {
                async: true
              }
            },
            {
              loader: require.resolve('css-loader'),
              options: {
                minimize: true
              }
            }
          ],
          test: /\.css$/
        },
        {
          use: [
            {
              loader: require.resolve('@microsoft/loader-cased-file'),
              options: {
                name: '[name:lower]_[hash].[ext]'
              }
            }
          ],
          test: new RegExp(`\.(${fileLoaderExts.map((e: string) => lodash.escapeRegExp(e)).join('|')})((\\?|\\#).+)?$`)
        },
        {
          use: [
            {
              loader: require.resolve('html-loader')
            }
          ],
          test: /\.html$/
        }
      ],
      noParse: [ /\.map$/ ]
    },
    resolve: {
      alias: {},
      modules: [
        'node_modules',
        'lib'
      ]
    }
  };

  constructor() {
    super(
      'configure-webpack',
      {
        libraryName: undefined,
        webpack: undefined,
        webpackPublicPathOptions: undefined,
        additionalConfiguration: undefined,
        configureExternalBundlingWebpackTask: undefined,
        statsObjectsDropLocation: undefined
      }
    );
  }

  public loadSchema(): Object {
    return require('./configure-webpack.schema.json');
  }

  public setConfig(config: IConfigureWebpackTaskConfig): void {
    if (config.webpack && config.webpack.name !== 'webpack') {
      this.logWarning('Setting webpack property to non-"@microsoft/gulp-core-build-webpack"-type will ' +
                      'prevent configuration');
    }

    super.mergeConfig(config);
  }

  public getKarmaConfig(extraRules: webpack.Rule[],
                        externals: webpack.ExternalsElement[],
                        karmaTask: KarmaTask): webpack.Configuration {
    const locAliases: { [id: string]: string } = {};
    try {
      const locResourceKeys: string[] = Object.keys(this.properties.discoveredLocalizedResources || {});
      const resolveResult: IResolveResourcesResult = this.resolveResources(locResourceKeys, undefined, true, karmaTask);
      const locale: string = resolveResult.locales[0];
      for (const resourceId of resolveResult.localizedResourcesToBundle) {
        // Appending "$" to the resource ID to ensure paths under it aren't matched. See
        //  https://webpack.js.org/configuration/resolve/#resolve-alias
        locAliases[`${resourceId}$`] = this.properties.discoveredLocalizedResources[resourceId][locale];
      }
    } catch (e) {
      karmaTask.logError(`Error resolving localized resources for test. Tests may fail unexpectedly. Error: ${e}`);
    }

    const testAddons: webpack.Configuration = {
      externals: externals,
      module: {
        rules: [
          {
            use: [
              {
                loader: 'imports',
                options: {
                  define: false
                }
              }
            ],
            test: /sinon\.js$/
          }
        ]
      },
      plugins: [new webpack.DefinePlugin({
        'process.env.NODE_ENV': JSON.stringify(this.buildConfig.production ? 'production' : 'dev'),
        DEBUG: !this.buildConfig.production,
        DEPRECATED_UNIT_TEST: true,
        NPM_BUILD: false,
        BUILD_NUMBER: '\'unit-test_0000-00-00.000\'',
        DATACENTER: !this.buildConfig.args['on-prem']
      })],
      resolve: {
        alias: locAliases
      }
    };

    (testAddons.module as webpack.NewModule).rules.push(...extraRules);

    return this._appendToBaseConfig(testAddons);
  }

  public executeTask(gulp: typeof Gulp, completeCallback: (error?: string) => void): NodeJS.ReadWriteStream {
    if (!this.taskConfig.webpack) {
      this.logWarning('"webpack" is not defined in the task configuration. Nothing to configure.');
      completeCallback();
      return;
    }

    const configValidationResult: string | undefined = this._validateEntries();
    if (configValidationResult) {
      completeCallback(configValidationResult);
      return;
    }

    if (!this.buildConfig.properties) {
      this._logNoEntriesWarning();
      this._setWebpackConfig({});
      completeCallback();
      return;
    }

    // The ConfigureExternalBundlingWebpackTask will disable the second webpack task
    // when doing a dev build or in a project without localized resources.
    // This, however, breaks gulp serve. Therefore, we should ensure the webpack task is activated.
    // @todo 261553 - this should be removed when we do this refactor
    this.taskConfig.webpack.isEnabled = () => true;

    const configBundleEntries: { [name: string]: IConfigBundle } = this.properties.bundles || {};

    const externalsKeys: string[] = getExternalsKeys(this);

    // Remove the specified linked externals to ensure they're bundled
    if (this.taskConfig.linkedExternalsToBundle && this.taskConfig.linkedExternalsToBundle.length) {
      for (const linkedExternalToBundle of this.taskConfig.linkedExternalsToBundle) {
        const linkedExternalToBundleIndex: number = externalsKeys.indexOf(linkedExternalToBundle);
        if (linkedExternalToBundleIndex !== -1) {
          externalsKeys.splice(linkedExternalToBundleIndex, 1);
        }
      }
    }

    const localizedResources: { [name: string]: string } = this.properties.localizedResources || {};
    const localizedResourcesKeys: string[] = Object.keys(localizedResources);

    const configBundleEntriesCount: number = Object.keys(configBundleEntries).length;
    if (configBundleEntriesCount === 0) {
      this._logNoEntriesWarning();
      this.taskConfig.webpack.isEnabled = () => false;
      completeCallback();
      return;
    }

    this.logVerbose(`${configBundleEntriesCount} entries specified.`);
    this.logVerbose(`${externalsKeys.length} externals specified.`);
    this.logVerbose(`${localizedResourcesKeys.length} localized resources specified.`);

    localizedResourcesKeys.forEach((key: string) => {
      if (externalsKeys.indexOf(key) !== -1) {
        this.logError(`Invalid Configuration: Key "${key}" is specified in both externals and localizedResources.`);
      }
    });

    const webpackPublicPathOptions: ISetWebpackPublicPathPluginOptions =
        lodash.clone(this.taskConfig.webpackPublicPathOptions) || {};
    if (!webpackPublicPathOptions.systemJs) {
      lodash.merge(webpackPublicPathOptions, {
        scriptName: {
          name: '[name]_?[a-zA-Z0-9-_]*\.js',
          isTokenized: true
        }
      } as ISetWebpackPublicPathPluginOptions);
    }

    // Users can use the --entry to control which entries actually get bundled
    // This value can match the GUID or the alias
    const entryParam: string = this.buildConfig.args['entry'] as string; // tslint:disable-line:no-string-literal

    this.properties.bundleEntries = [];
    const webpackEntries: { [name: string]: string } = {};
    let foundEntry: boolean;

    for (const bundleName in configBundleEntries) {
      if (configBundleEntries.hasOwnProperty(bundleName)) {
        const configEntry: IConfigBundle = configBundleEntries[bundleName];
        let shouldBundleEntry: boolean = entryParam === undefined;
        const bundleEntry: IBundleEntry = {
          bundleName,
          nonLocalizedEntrypointPath: path.join(this.outputDirectory, `${bundleName}.js`),
          components: {}
        };

        for (const componentEntry of configEntry.components) {
          const manifestData: IClientSideComponentManifest = this.getManifest(componentEntry.manifest);

          // If the manifest version is "*", replace it with the package version. Do this here to make sure the
          //  bundle's name doesn't contain the "*" version
          if (manifestData.version === '*') {
            const manifestVersion: string = this.getPackageVersion();
            manifestData.version = manifestVersion;
          }

          shouldBundleEntry = shouldBundleEntry ||
            ( entryParam && manifestData &&
              ( manifestData.id.toLowerCase() === entryParam.toLowerCase() ||
                manifestData.alias.toLowerCase() === entryParam.toLowerCase() ) );

          if (entryParam === undefined || shouldBundleEntry) {
            foundEntry = true;

            // Keep a counter of the manifests inside the bundle so we can set the exportName property in the produced
            //  manifest(s) and generate a JS file to bundle both components' entrypoints
            bundleEntry.components[manifestData.id] = {
              ...componentEntry,
              manifestData,
              manifestPath: path.join(this.buildConfig.rootPath, componentEntry.manifest)
            };
          }
        }

        if (shouldBundleEntry) {
          const componentKeys: string[] = Object.keys(bundleEntry.components);
          if (componentKeys.length === 1) {
            // Single component, just point to its entrypoint in the webpack config
            webpackEntries[bundleName] = path.join(
              this.buildConfig.rootPath,
              bundleEntry.components[componentKeys[0]].entrypoint
            );
          } else {
            // Multiple components, we need to generate an entry to point the webpack config at
            webpackEntries[bundleName] = this._generateBundleEntrypointFile(bundleEntry);
          }

          this.properties.bundleEntries.push(bundleEntry);
        }
      }
    }

    if (entryParam && !foundEntry) {
      completeCallback(`The "--entry" flag was specified, but "${entryParam}" did not match ` +
        `the id or alias of any entries listed in config.json`);
      return;
    }

    this.properties.webpackLibraryName = this.taskConfig.libraryName || (() => {
      if (this.properties.bundleEntries.length === 0) {
        // We're not producing any bundles, so we don't need to worry about the library name
        return undefined;
      } else if (this.properties.bundleEntries.length === 1) {
        // We're producing one bundle, so we can generate a simple name
        return getBundleId(this.properties.bundleEntries[0]);
      } else {
        // There are multiple bundles. Because we need something unique for the webpack JSONP function, we'll just
        //  concatenate the IDs and then hash them. In the post-bundle steps, we'll replace the "define(<name>, ..."
        //  name with the real IDs. When we update this task to support multiple webpack configurations, we can
        //  separate each bundle to have its own library name.
        this.properties.webpackLibraryNameIsTemporary = true;
        return getFullHash(this.properties.bundleEntries.map((bundle) => getBundleId(bundle)).join(''));
      }
    })();

    const configAddons: webpack.Configuration = {
      context: this.buildConfig.rootPath,
      devtool: this.buildConfig.production ? undefined : 'source-map',
      entry: webpackEntries,
      externals: externalsKeys.concat(localizedResourcesKeys),
      module: {
        rules: [
          {
            test: /\.js$/,
            enforce: 'pre',
            use: require.resolve('source-map-loader')
          }
        ]
      },
      output: {
        chunkFilename: '[id].[name]_[chunkhash].js',
        filename: '[name].js',
        library: this.properties.webpackLibraryName,
        libraryTarget: this.taskConfig.libraryTarget || 'amd',
        path: this.outputDirectory,
        devtoolModuleFilenameTemplate: 'webpack:///../[resource-path]',
        devtoolFallbackModuleFilenameTemplate: 'webpack:///../[resource-path]?[hash]'
      },
      plugins: [
        new SetPublicPathPlugin(webpackPublicPathOptions)
      ]
    };

    if (this.buildConfig.production) {
      configAddons.plugins.push(new webpack.optimize.UglifyJsPlugin({
        compress: {
          dead_code: true,
          warnings: false
        },
        mangle: true
      }));
    }

    const buildNumber: string = process.env.BUILD_BUILDNUMBER
      ? `'${process.env.BUILD_BUILDNUMBER.toLowerCase()}'`
      : '\'dev-0000-00-00.000\'';

    configAddons.plugins.push(new webpack.DefinePlugin({
      'process.env.NODE_ENV': JSON.stringify(this.buildConfig.production ? 'production' : 'dev'),
      DEBUG: !this.buildConfig.production,
      DEPRECATED_UNIT_TEST: false,
      NPM_BUILD: this.npmBuild,
      BUILD_NUMBER: buildNumber,
      DATACENTER: !this.buildConfig.args['on-prem']
    }));

    let config: webpack.Configuration = this._appendToBaseConfig(configAddons);
    if (this.taskConfig.configureExternalBundlingWebpackTask) {
      this.taskConfig.configureExternalBundlingWebpackTask.tinkerWithBaseWebpackConfig(config);
    }

    if (this.taskConfig.additionalConfiguration) {
      config = this.taskConfig.additionalConfiguration(config);
    }

    this._setWebpackConfig(config);

    completeCallback();
    return;
  }

  /**
   * Modify the localized webpack config to make sure the end result is correct.
   *
   * @internal
   */
  public tinkerWithLocConfig(config: webpack.Configuration): void {
    config.output.libraryTarget = this.taskConfig.libraryTarget || 'amd';
  }

  private _appendToBaseConfig(config: webpack.Configuration): webpack.Configuration {
    return lodash.mergeWith({}, this._baseConfig, config, (value1: any, value2: any) => { // tslint:disable-line:no-any
      if (lodash.isArray(value1) && lodash.isArray(value2)) {
        return lodash.concat(value1, value2);
      } else {
        return undefined;
      }
    });
  }

  private _logNoEntriesWarning(): void {
    this.logWarning('No entries are defined, so no bundles will be produced.');
  }

  private _setWebpackConfig(config: webpack.Configuration): void {
    const webpackTask: WebpackTask = this.taskConfig.webpack;
    const sourceMapWarningSuppression: RegExp = /Cannot find SourceMap \'.+\'\: Error\:/;
    const suppressedWarnings: (string | RegExp)[] = webpackTask.taskConfig.suppressWarnings || [];

    if (webpackTask) {
      webpackTask.setConfig({
        config: config as any, // tslint:disable-line:no-any
        configPath: null, // tslint:disable-line:no-null-keyword
        suppressWarnings: suppressedWarnings.concat([sourceMapWarningSuppression])
      });
    }
  }

  private _validateEntries(): string | undefined {
    if (this.properties.bundles) {
      const entrypointPaths: Set<string> = new Set<string>();
      const manifestPaths: Set<string> = new Set<string>();

      for (const bundleName in this.properties.bundles) {
        if (this.properties.bundles.hasOwnProperty(bundleName)) {
          const bundleEntry: IConfigBundle = this.properties.bundles[bundleName];
          for (const component of bundleEntry.components) {
            const entrypointPath: string =
              path.resolve(path.join(this.buildConfig.rootPath, component.entrypoint)).toUpperCase();
            if (entrypointPaths.has(entrypointPath)) {
              return `Entry path "${entrypointPath}" occurs in multiple config.json components. This is not supported.`;
            } else {
              entrypointPaths.add(entrypointPath);
            }

            const manifestPath: string =
              path.resolve(path.join(this.buildConfig.rootPath, component.manifest)).toUpperCase();
            if (manifestPaths.has(manifestPath)) {
              return `Manifest path "${manifestPath}" occurs in multiple config.json components. This is not ` +
                     'supported.';
            } else {
              manifestPaths.add(manifestPath);
            }
          }
        }
      }
    }
  }

  /**
   * Writes the entrypoint file to the temp directory and returns the path to the file.
   */
  private _generateBundleEntrypointFile(bundleEntry: IBundleEntry): string {
    const filePath: string = path.join(
      this.buildConfig.rootPath,
      this.buildConfig.tempFolder,
      'bundle-entries',
      `${bundleEntry.bundleName}.js`
    );

    const fileContents: string = [
      'Object.defineProperty(exports, "__esModule", { value: true });',
      ...Object.keys(bundleEntry.components).map((id) => {
        const fullEntrypointPath: string = path.join(this.buildConfig.rootPath, bundleEntry.components[id].entrypoint);
        return `exports['${id}'] = require('${fullEntrypointPath.replace(/\\/g, '\\\\')}');`;
      })
    ].join(EOL);
    fsx.ensureDirSync(path.dirname(filePath));
    fsx.writeFileSync(filePath, fileContents);

    return filePath;
  }
}
